import React from 'react';
import { Link, graphql } from 'gatsby';
import Layout from '../components/layout';
// import ExternalLink from '../components/ExternalLink';
import SEO from '../components/seo';

export default 'lol';

export const A = ({ data, pageContext }) => {
    const post = data.markdownRemark;
    // const { blogContributor } = data.site.siteMetadata;
    const { previous, next } = pageContext;

    return (
        <Layout title={post.frontmatter.title}>
            <SEO description={post.frontmatter.description || post.excerpt} />
            <article>
                <header>
                    {/*<p>
                        {post.frontmatter.date}. Written by{' '}
                        <ExternalLink href={blogContributor.url}>
                            {blogContributor.name}
                        </ExternalLink>
                    </p>*/}
                </header>
                <section dangerouslySetInnerHTML={{ __html: post.html }} />
                <hr />
            </article>

            <nav>
                <ul
                    style={{
                        display: `flex`,
                        flexWrap: `wrap`,
                        justifyContent: `space-between`,
                        listStyle: `none`,
                        padding: 0
                    }}
                >
                    <li>
                        {previous && (
                            <Link to={previous.fields.slug} rel="prev">
                                ← {previous.frontmatter.title}
                            </Link>
                        )}
                    </li>
                    <li>
                        {next && (
                            <Link to={next.fields.slug} rel="next">
                                {next.frontmatter.title} →
                            </Link>
                        )}
                    </li>
                </ul>
            </nav>
        </Layout>
    );
};

export const pageQuery = graphql`
    query BlogPostBySlug($slug: String!) {
        site {
            siteMetadata {
                blogContributor {
                    name
                    url
                }
            }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
            id
            excerpt(pruneLength: 160)
            html
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
            }
        }
    }
`;
